'use strict';

(function () {
  //top menu scroll to
  var menuLinks = document.querySelectorAll('.js-head-menu a');
  Array.prototype.forEach.call(menuLinks, function (el, i) {
    el.addEventListener('click', function () {
      // event.preventDefault();
      var menuTarget = el.getAttribute('href');
      var menuTargetBlock = document.querySelector(menuTarget);
      scrollIt(menuTargetBlock, 2000);
    });
  });
})();